body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.datetime {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: auto;
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  font-size: 20px;
  padding: 10px 80px;
}

.navbar {
  width: 100%;
  height: auto;
  background-color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.75);
}
.navbar .logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40%;
  color: rgb(0, 0, 0);
  padding: 20px 20px 20px 50px;
}

.navbar .menu {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60%;
}
.navbar .menu a {
  color: rgb(0, 0, 0);
  text-decoration: none;
  font-weight: bold;
  padding: 25px;
}

.navbar .menu a:hover,
.navbar .logo:hover {
  color: #ba68c8;
  background-color: rgb(255, 255, 255);
  transition: ease-in-out 0.5s;
}


.main {
  display: flex;
  /* flex-direction: column; */
  flex-wrap: wrap;
  margin: 10px 170px;
}
.card {
  margin: 0 10px;
  padding: 10px;
  width: 250px;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card_discription {
  margin-top: -10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: rgb(223, 223, 223);
  border-bottom: solid 4px #ba68c8;
}
.card_discription h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: large;
  padding: 10px;
}
.card_discription a {
  width: auto;
  margin: 0 10px 20px 10px;
  height: 40px;
  font-size: 18px;
  background: #ba68c8;
  font-weight: bold;
  color: rgb(255, 255, 255);
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card_discription a:hover {
  background: #e58ff4;
}

.home {
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.home img {
  width: 500px;
  margin: 10px 100px 0 0;
}
.homeleft {
  display: flex;
  flex-direction: column;
}
.homeleft h2 {
  font-size: 45px;
  font-weight: 700;
  color: #000000;
  margin-bottom: 20px;

  margin: 0 180px;
}
.homeleft h2 span {
  color: #ba68c8;
}

.homeleft p {
  font-size: 20px;
  font-weight: 400;
  color: #000000;
  margin-bottom: 20px;
  margin: 30px 180px;
}

button {
  margin: 0px 180px;
  width: 200px;
  height: 50px;
  border-radius: 50px;
  background-color: white;
  border: 3px solid #000000;
  font-size: 17px;

}
button:hover {
  background-color: #000000;
  color: white;
  transition: ease-in-out 0.5s;
}

